{
  const elemCirc = document.getElementById("circ");
  var token = getFromLocalStorage("token");

  if (token) {
    window.location.href = "../profile-select/index.html";
  }

  let interval;

  async function main() {
    let data = await axios("https://watch.tv.br/api/auth/simplified");
    data = data.data;
    document.getElementById("alfaCode").innerHTML = data.code;
    getVerify(data.code);

    return data.timeout;
  }

  window.addEventListener("auxclick", function (e) {
    if (e.button == 1) {
      enterAction();
    }
  });

  function goBack() {
    history.back();
  }

  window.onload = function () {
    document.addEventListener("keydown", function (e) {
      switch (e.keyCode) {
        case 8:
          e.preventDefault();
          goBack();
          break;

        case 461:
          window.history.back();
          break;
        case 13:
          enterAction();
          break;
        default:
          break;
      }
    });
  };

  window.addEventListener("wheel", function (event) {
    if (event.deltaY < 0) {
      arrowUp();
    } else if (event.deltaY > 0) {
      arrowDown();
    }
  });

  function enterAction() {
    goTo("../../index.html");
  }

  window.goTo = goTo;

  function restartInterval(timeout) {
    const apiTimeout = new Date(timeout);
    const dateTime = new Date();

    const currentTimeH = 3600000 * dateTime.getHours();
    const currentTimeM = 60000 * dateTime.getMinutes();
    const apiNextTimeH = 3600000 * apiTimeout.getHours();
    const apiNextTimeM = 60000 * apiTimeout.getMinutes();

    const time = Math.abs(
      currentTimeH + currentTimeM - (apiNextTimeH + apiNextTimeM)
    );
    const quickTime = time / 100;
    let percent = 100;

    interval = setInterval(() => {
      percent -= 1;
      elemCirc.style.strokeDashoffset = `calc(252px - (252px * ${percent}) / 100)`;
    }, quickTime);

    setTimeout(async () => {
      clearInterval(interval);
      const timeout = await main();
      restartInterval(timeout);
    }, time);
  }

  async function getVerify(data) {
    setInterval(async () => {
      let verify = await getResponse(
        "https://watch.tv.br/api/auth/simplified/verify?key=" + data
      );
      getLogin(verify);
    }, 3000);
  }

  function getLogin(verify) {
    let idPerfil = String(verify.profile);
    if (!verify.wait) {
      if (!verify.token) {
        goTo("./index.html");
      }
      setLocalStorage("token", verify.token);
      setLocalStorage("id_perfil", idPerfil);
      goTo("../profile-select/index.html");
    }
  }

  window.onkeyup = function () {
    document.querySelector(".passwordButton").focus();
  };

  async function getResponse(url) {
    let request = await axios(url, { method: "POST" });
    return request.data;
  }

  function goTo(path) {
    let place = (window.location.href = path);

    return place;
  }

  function setLocalStorage(key, thing) {
    let item = localStorage.setItem(key, thing);

    return item;
  }

  function getFromLocalStorage(key) {
    let item = localStorage.getItem(key);

    return item;
  }

  restartInterval();
}

// Credit: Mateusz Rybczonec

const FULL_DASH_ARRAY = 283;
const WARNING_THRESHOLD = 10;
const ALERT_THRESHOLD = 5;

const COLOR_CODES = {
  info: {
    color: "green",
  },
  warning: {
    color: "orange",
    threshold: WARNING_THRESHOLD,
  },
  alert: {
    color: "red",
    threshold: ALERT_THRESHOLD,
  },
};

const TIME_LIMIT = 300;
let timePassed = 0;
let timeLeft = TIME_LIMIT;
let timerInterval = null;
let remainingPathColor = COLOR_CODES.info.color;

document.getElementById("app").innerHTML = `
<div class="base-timer">
  <svg class="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
    <g class="base-timer__circle">
      <circle class="base-timer__path-elapsed" cx="50" cy="50" r="45"></circle>
      <path
        id="base-timer-path-remaining"
        stroke-dasharray="283"
        class="base-timer__path-remaining ${remainingPathColor}"
        d="
          M 50, 50
          m -45, 0
          a 45,45 0 1,0 90,0
          a 45,45 0 1,0 -90,0
        "
      ></path>
    </g>
  </svg>
  <span id="base-timer-label" class="base-timer__label">${formatTime(
  timeLeft
)}</span>
</div>
`;

startTimer();

function onTimesUp() {
  clearInterval(timerInterval);
}

function startTimer() {
  timerInterval = setInterval(() => {
    timePassed = timePassed += 1;
    timeLeft = TIME_LIMIT - timePassed;
    document.getElementById("base-timer-label").innerHTML =
      formatTime(timeLeft);
    setCircleDasharray();
    setRemainingPathColor(timeLeft);

    if (timeLeft === 0) {
      onTimesUp();
    }
  }, 1000);
}

function formatTime(time) {
  const minutes = Math.floor(time / 60);
  let seconds = time % 60;

  if (seconds < 10) {
    seconds = `0${seconds}`;
  }

  return `${minutes}:${seconds}`;
}

function setRemainingPathColor(timeLeft) {
  const { alert, warning, info } = COLOR_CODES;
  if (timeLeft <= alert.threshold) {
    document
      .getElementById("base-timer-path-remaining")
      .classList.remove(warning.color);
    document
      .getElementById("base-timer-path-remaining")
      .classList.add(alert.color);
  } else if (timeLeft <= warning.threshold) {
    document
      .getElementById("base-timer-path-remaining")
      .classList.remove(info.color);
    document
      .getElementById("base-timer-path-remaining")
      .classList.add(warning.color);
  }
}

function calculateTimeFraction() {
  const rawTimeFraction = timeLeft / TIME_LIMIT;
  return rawTimeFraction - (1 / TIME_LIMIT) * (1 - rawTimeFraction);
}

function setCircleDasharray() {
  const circleDasharray = `${(
    calculateTimeFraction() * FULL_DASH_ARRAY
  ).toFixed(0)} 283`;
  document
    .getElementById("base-timer-path-remaining")
    .setAttribute("stroke-dasharray", circleDasharray);
}
